import { Text } from '@consta/uikit/Text';
import { Button } from '@consta/uikit/Button';
import { Card } from '@consta/uikit/Card';
import { Slider } from '@consta/uikit/Slider';
import styles from './styles.module.css';
import music from '../../assets/music/Mion - Progress.mp3';
import { IconPlay } from '@consta/icons/IconPlay';
import { IconPause } from '@consta/icons/IconPause';
import { IconArrowNext } from '@consta/icons/IconArrowNext';
import { IconArrowPrevious } from '@consta/icons/IconArrowPrevious';
import { useState, useRef, useEffect } from 'react';

export const MusicPlayer = ({ innerRef }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const audioRef = useRef();
  const [currentTime, setCurrentTime] = useState(0);

  useEffect(() => {
    if (isPlaying) {
      audioRef.current.play();
    } else {
      audioRef.current.pause();
    }
  }, [isPlaying, audioRef]);

  // useEffect(() => {
  //   sliderPoint.current = document.getElementsByClassName('SliderPoint')[0];
  //   sliderLine.current = document.getElementsByClassName('SliderLine-Line')[0];
  // }, []);

  // useEffect(() => {
  //   console.log(sliderPoint.current.ariaValueNow);
  // }, [
  //   () => {
  //     if (sliderPoint !== undefined && sliderLine.current !== undefined)
  //       return sliderPoint.current.ariaValueNow;
  //   }
  // ]);

  function buttons() {
    if (!isPlaying) {
      return (
        <Button
          iconLeft={IconPlay}
          onlyIcon
          view="clear"
          onClick={() => setIsPlaying(true)}
          className={styles.btn}
        />
      );
    } else {
      return (
        <Button
          iconLeft={IconPause}
          onlyIcon
          view="clear"
          onClick={() => setIsPlaying(false)}
          className={styles.btn}
        />
      );
    }
  }

  function formatTime() {
    if (audioRef.current !== undefined) {
      // var time = audioRef.current.currentTime;
      var time = currentTime;
      const minutes = Math.floor(time / 60);
      const formatMinutes = minutes < 10 ? `0${minutes}` : `${minutes}`;
      const seconds = Math.floor(time % 60);
      const formatSeconds = seconds < 10 ? `0${seconds}` : `${seconds}`;
      return `${formatMinutes}:${formatSeconds}`;
    }
    return '00:00';
  }

  function setTime(time) {
    if (audioRef.current !== undefined) {
      audioRef.current.currentTime = time;
      // setIsSlider(false);
    }
  }

  return (
    <div className={styles.musicPlayer} ref={innerRef}>
      <Text className={styles.title}>Музыка</Text>
      <Card className={styles.playerCard} shadow={false}>
        <div className={styles.controls}>
          <Button
            iconLeft={IconArrowNext}
            onlyIcon
            view="clear"
            className={styles.btn}
          />
          {buttons()}
          <Button
            iconLeft={IconArrowPrevious}
            onlyIcon
            view="clear"
            className={styles.btn}
          />
          <Text size="l" style={{ marginLeft: '5px' }}>
            Мион - До чего дошел прогресс
          </Text>
        </div>
        <div className={styles.controls}>
          <Text size="s">{formatTime()}</Text>
          <Slider
            min={0}
            max={895}
            withTooltip
            step={1}
            value={currentTime}
            className={styles.slider}
            tooltipFormatter={(value) => formatTime(value)}
            onChange={({ value }) => {
              // setIsSlider(true);
              setCurrentTime(value);
              setTime(value);
            }}
            style={{ cursor: 'pointer' }}
          />
          <Text size="s">14:55</Text>
        </div>
      </Card>
      <audio
        src={music}
        // controls
        preload="auto"
        ref={audioRef}
        onTimeUpdate={() => {
          //if (!isSlider) {
          // setTime(currentTime)
          setCurrentTime(audioRef.current.currentTime);
          //}
        }}
        loop
      />
    </div>
  );
};
