import { Button } from '@consta/uikit/Button';
import { ContextMenu } from '@consta/uikit/ContextMenu';
import styles from './styles.module.css'
import { useState, useRef } from 'react';
import { useNavigate } from 'react-router-dom';


export const TopMenu = () => {
  const [isOpen, setIsOpen] = useState(false)
  const ref = useRef(null);
  const navigate =  useNavigate()
  const items = [
  {label: 'Единое окно ДРД'},
  {label: 'Стратегия', event:() => navigate("/management")},
  {label: 'Ресурсы', event:() => navigate("/management")},
  {label: 'Менеджмент', event:() => navigate("/management")}]
  const getItemOnClick =(item)=>{
     if (item.event) {
      return item.event;
    }
  }
  return <div>    
    <Button label="Менеджмент" view="clear" className={styles.menuBtn} onClick={() => navigate("/management")}/>
    <Button label="Ресурсы" view="clear" className={styles.menuBtn} onClick={() => navigate("/management")}/>
    <Button label="Стратегия" view="clear" className={styles.menuBtn} onClick={() => navigate("/management")}/>    
    <>
    <Button  label="Другие направления" view="clear" onClick={() => setIsOpen(!isOpen)} ref={ref}/>
    <ContextMenu 
      label="Другие направления" 
      isOpen={isOpen}
      items={items} 
      getItemLabel={(item) => item.label}
      anchorRef={ref}  
      getItemOnClick={getItemOnClick}
      onClickOutside={() => setIsOpen(false)}></ContextMenu>
    </>    
  </div>
}