import { createSlice } from "@reduxjs/toolkit"

export const sideBarIsOpenSlice = createSlice({
    name: 'sideBarIsOpenSlice',
    initialState: {
      sideBarIsOpen: true
    },
    reducers: {
      setSideBarIsOpenSlice: (state, action) => {
        state.sideBarIsOpen = action.payload
      },
    },
});

export const { setSideBarIsOpenSlice } = sideBarIsOpenSlice.actions;
export default sideBarIsOpenSlice.reducer;