import styles from './styles.module.css'

export const DragNDrop = ({fileDroped, handleDragLeave}) => {
  const handleDrop = function(e) {
  e.preventDefault();
  e.stopPropagation();

  if (e.dataTransfer.files && e.dataTransfer.files[0]) {
    fileDroped(e.dataTransfer.files[0])
  }
};
const handleDrag = function(e){
  e.preventDefault();
  e.stopPropagation();
  handleDragLeave(e);
}
  return <div className={styles.DragContainer} onDrop={handleDrop}  onDragLeave={handleDrag}>
    <div className={styles.DragBordered}>
      <div className={styles.DragText}>Перетащите файлы сюда для их отправки</div>
      <div className={styles.DragFormat}>Поддерживаемые форматы: TXT, PDF, DOC, DOCX, XLS, XLSX, CSV</div>
    </div>
  </div>
}