import { ThemeContext } from '../../context/ThemeContext';
import React, { useContext } from 'react';
import { Layout } from '@consta/uikit/Layout';
import { MainPageHeader } from "../MainPageHeader/MainPageHeader";
import { MainPageSidebar } from "../MainPageSidebar/MainPageSidebar";
import { Theme, presetGpnDark, presetGpnDefault } from '@consta/uikit/Theme';
import { useSelector } from 'react-redux';
import { ErrorMesage } from '../../components/ErrorMessage/ErrorMessage';
import classnames from "classnames";
import styles from './styles.module.css'

export const MainLayout = ({ children }) => {
  const {theme} = useContext(ThemeContext)
  const sideBarIsOpen = useSelector((state) => state.sideBarIsOpen.sideBarIsOpen)
  const error = useSelector((state) => state.errorMessage.error)
  const getTheme = (item) => {
    return item === 'Default' ? presetGpnDefault : presetGpnDark
  }
  return (
    <Theme preset={getTheme(theme)}>      
      <Layout direction="row" className={classnames({ [styles.sideBarClosed]: !sideBarIsOpen}, styles.mainLayout)}>
        <div className={styles.sideBarWrapper}>
          <MainPageSidebar/>
        </div>             
        <Layout direction="column" className={styles.mainContainer}>
          <div className={styles.mainHeader}>
            <MainPageHeader />
          </div> 
          <div className={styles.contentWrapper}>
            {children}        
          </div>            
      </Layout> 
         {error && <ErrorMesage/>}       
      </Layout> 
    </Theme>   
  )
}