import { createSlice } from "@reduxjs/toolkit"

export const openMenuLinkSlice = createSlice({
    name: 'openMenuLinkSlice',
    initialState: {
        menuLink: 'chat'
    },
    reducers: {
        setMenuLinkSlice: (state, action) => {
            state.menuLink = action.payload
        },
    },
});

export const { setMenuLinkSlice } = openMenuLinkSlice.actions;
export default openMenuLinkSlice.reducer;