import styles from './styles.module.css';
import axios from 'axios';
import { IconSendMessage } from '@consta/uikit/IconSendMessage';
import { IconAttach } from '@consta/uikit/IconAttach';
import React, { useState, useRef, useEffect } from 'react';
import { ChatRow } from '../ChatRow/ChatRow';
import { DragNDrop } from '../DragNDrop/DragNDrop';
import { ChatInfo } from '../ChatInfo/ChatInfo'
import { useSelector } from "react-redux"
import { FileUpload } from "../FileUpload/FileUpload"
import classNames from 'classnames';
import { store } from '../../store.js'
import { setError,  setErrorMessage} from '../../features/errorMessageSlice';

let nextId = 0;
var height = 0;
var scrollHeight = 0;
var offsetHeight = 0;
const userId = (Math.random() + 1).toString(36).substring(7);

export const Chat = () => {
  // const apiUrl = 'http://146.185.234.11:8000/chat/';
  const apiUrl = 'https://mion.koa.gazprom-neft.ru/back/';
  const fileUploadUrl = "http://146.185.234.11/upload/1";
  const sideBarOpen = useSelector((state) => state.sideBarIsOpen.sideBarIsOpen)
  //const [value, setValue] = useState('');
  const [inputValue, setInputValue] = useState('');
  const [messages, setMessages] = useState([]);
  const [loading, setLoading] = useState(false);
  const [isDisabled, setIsDisabled] = useState(true)
  const [isFile, setIsFile] = useState(null)
  const [isSkill, setIsSkill] = useState(false)
  const [dragActive, setDragActive] = useState(false);
  let value = ''


  // handle drag events
  const handleDrag = function(e) {
    e.preventDefault();
    e.stopPropagation();
    if (e.type === "dragenter" || e.type === "dragover") {
      setDragActive(true);
     }
  };

  const handleDragLeave =  function(e) {
    e.preventDefault();
    e.stopPropagation();
    setDragActive(false);
  };
  const fileDroped = (e) => {
    let ext = e.name.split(".")
    ext = ext[ext.length - 1]
    if(e.size > 2097152){
      store.dispatch(setError(true))
      store.dispatch(setErrorMessage("Загрузите файл до 20 МБ"))
       setIsFile(null)
      }
    else if( ext !== 'txt' && ext !== 'pdf' && ext !== 'doc' && ext !== 'docx'
    && ext !== 'xls' && ext !== 'xlsx' && ext !== 'csv'){
      store.dispatch(setError(true))
      store.dispatch(setErrorMessage("Поддерживаемые форматы: TXT, PDF, DOC, DOCX, XLS, XLSX, CSV"))
      setIsFile(null)
    }
      else{
        setIsFile(e);
      }
    setDragActive(false);
  }
  const handleChange = (e) => {
    if(e.target.files[0]){
      if(e.target.files[0].size > 2097152){
       store.dispatch(setError(true))
        store.dispatch(setErrorMessage("Загрузите файл до 20 МБ"))
       setIsFile(null)
      }
      else{
        setIsFile( e.target.files[0])
      }      
    }};

  function timestamp() {
    let time = new Date(Date.now());
    let hours = time.getHours();
    let minutes = time.getMinutes();
    return `${hours < 10 ? '0' + hours : hours}:${
      minutes < 10 ? '0' + minutes : minutes
    }`;
  }

  function setIds(tableString) {
    const tableArray = JSON.parse(tableString);
    for (let i = 0; i < tableArray.length; i++) {
      tableArray[i] = { id: i, ...tableArray[i] };
    }
    return tableArray;
  }

  const input = useRef(null);

  function ckickButton(){   
    if(!value || value === ""){ 
      value = input.current.textContent
    } 
    sendRequest();
  }

  function sendRequest() {    
    const data = {
      user_id: userId,
      payload: value
    };
    if(isFile){  
      setMessages((messages) => [
      ...messages,
      { id: nextId++, speaker: 'user', isFileName: isFile.name, isFileSize: formatBytes(isFile.size), message: value, timestamp: timestamp() }
    ]); 
      fetchFileResponse(isFile);  
      setIsFile(null)
      value = '';
      input.current.textContent = ''
      setInputValue('')
      return
    }  
    setMessages((messages) => [
      ...messages,
      { id: nextId++, speaker: 'user', message: value, timestamp: timestamp() }
    ]);   
    //value = '';
    input.current.textContent = ''
    setInputValue('')
    // console.log(data.payload);
    // let url = apiUrl + value;
    fetchResponse(data);    
  }

  async function fetchResponse(data){
    setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: null,
            table: null,
            chart: null,
            fileUrl: null,
            extension: null,
            timestamp: null
          }
    ])   
    setLoading(true)
    await axios.post(apiUrl, data).then((response) => {
      setLoading(false)
      setMessages((messages) => messages.filter(message => message.message !== null))      
      let index = response.data.response.indexOf('[{');
      let lastIndex = response.data.response.indexOf('}]');
      let chartIndex = response.data.response.indexOf('<g>')
      let text;
      let table;  
      let chart;    
      if (index > -1) {
        text = response.data.response.substring(0, index);
        table = response.data.response.substring(index, lastIndex+2); 
        if(chartIndex > -1){
          chart = response.data.response.substring(chartIndex+3).trim(); 
        }
        setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: text,
            table: setIds(table),            
            fileUrl: null,
            extension: null,
            chart: chart,
            timestamp: timestamp()
          }
        ]);
      } else if (
        response.data.response.indexOf('/download') > -1 &&
        response.data.response.split('/download').length - 1 === 1
      ) {
        let index = response.data.response.indexOf('/download');
        let text;
        let fileUrl;
        let extension;
        if (index > -1) {
          text = response.data.response.substring(0, index);
          fileUrl = response.data.response.substring(index);
          extension = fileUrl.substring(fileUrl.lastIndexOf('.') + 1);
        }
        setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: text,
            table: null,
            chart: null,
            fileUrl: fileUrl,
            extension: extension,
            timestamp: timestamp()
          }
        ]);
      } else {
        setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: response.data.response,
            table: null,
            chart: null,
            fileUrl: null,
            extension: null,
            timestamp: timestamp()
          }
        ]);
      }
    });   
  }
  function scroll() {
    const chat = document.getElementById('chat');
    if (chat) chat.scrollTop = chat.scrollHeight;
  }

  



  useEffect(() => {
    scroll();
    // setDummy();
  }, [messages]);

  useEffect(() => {    
    if(((inputValue !== null && inputValue?.trim() !== '') || isFile) && !loading){
      setIsDisabled(false)
    } else {
      setIsDisabled(true)
    }
  }, [loading, isFile, inputValue])

  function keyboardEvents(event, isFile, isDisabled){
    if(event.key === "Enter"){
      event.preventDefault() 
      if(!isDisabled){
         if(isFile){
          value = input.current.textContent
          sendRequest()
        }
        else if(input.current.textContent !== ''){
          value = input.current.textContent
          sendRequest()
        }
      }   
    }
    
  }
  function pasteHandler(e){
    e.preventDefault();
    var text = e.clipboardData.getData("text/plain");
    document.execCommand("insertHTML", false, text);
  }
  // function listenerEnter(event) {
  //    if(event.key === 'Enter'){      
  //     if(input.current.textContent !== '' || value !== ''){
  //       event.preventDefault()
  //       if(!value || value === ""){ 
  //         value = input.current.textContent
  //       }
  //       sendRequest()
  //     }      
  //   }  
  // }

  // useEffect(() => { 
  //   document.addEventListener('keypress', listenerEnter) 
  // }, [])


  function setDummy() {
    const chat = document.getElementById('chat');
    if (chat) {
      if (scrollHeight <= chat.scrollHeight) scrollHeight = chat.scrollHeight;
      if (offsetHeight <= chat.offsetHeight) offsetHeight = chat.offsetHeight;
    }
    if (chat && height <= scrollHeight) {
      height = scrollHeight;
    }
    if (chat && height <= offsetHeight * 2) {
      return <div className={styles.dummy}></div>;
    }
  }

  function askQuestion(askValue){   
    input.current.focus()
    input.current.textContent = askValue
    setInputValue(askValue)
    setIsSkill(false)
    value = askValue
  }

  function formatBytes(bytes, decimals = 2) {
	if (bytes === 0) {
		return '0';
	} else {
		var k = 1024;
		var dm = decimals < 0 ? 0 : decimals;
		var sizes = ['байт', 'КБ', 'МБ', 'ГБ', 'ТБ'];
		var i = Math.floor(Math.log(bytes) / Math.log(k));
		return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
	}
}

function cancelFile(){
  setIsFile(null)
}

async function fetchFileResponse(file){
  setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: null,
            chart: null,
            table: null,
            fileUrl: null,
            extension: null,
            timestamp: null
          }
    ])   
    setLoading(true)
  let data = new FormData()
  data.append('file', file)
  try {
    await axios.post(fileUploadUrl, data).then( 
    response => {
      setLoading(false)
      setMessages((messages) => messages.filter(message => message.message !== null)) 
      setMessages((messages) => [
          ...messages,
          {
            id: nextId++,
            speaker: 'mion',
            message: "Вы загрузили файл",
            table: null,
            chart:null,
            fileUrl: null,
            extension: null,
            timestamp: timestamp()
          }
        ]); 
    }
  )
  }catch(error){
    store.dispatch(setError(true))
    store.dispatch(setErrorMessage("Не удалось загрузить файл"))
    setLoading(false)
    setMessages((messages) => messages.filter(message => message.message !== null)) 
  }
  
}

  return (
    <div className={styles.chat} onDragOver={handleDrag}  onDragEnter={handleDrag} onDragLeave={handleDrag}>
      <div className={styles.chatScroller} id="chat">
        {messages.length <=0 && <ChatInfo askQuestion={askQuestion}/>}
        {messages.length > 0 && setDummy()}
        {messages.map((message) => (
          <ChatRow
            sideBarOpen={sideBarOpen}
            key={message.id}
            speaker={message.speaker}
            text={message.message}
            timestamp={message.timestamp}
            table={message.table}
            fileUrl={message.fileUrl}
            extension={message.extension}
            isFileName={message.isFileName}
            isFileSize={message.isFileSize}
            chart={message.chart}
            isLastOfUs={
              messages.indexOf(message) === messages.length - 1 &&
              message.speaker === 'mion'
                ? true
                : false
            }
          />
        ))}
      </div>
      {dragActive && <DragNDrop fileDroped={fileDroped} handleDragLeave={handleDragLeave}/>}
      <div className={styles.inputBar}> 
        {isSkill && <div className={styles.skills}>
          <button className={styles.skillItem} onClick={e=>askQuestion("Поиск информации и получение консультаций")}>
            <span className={styles.skillName}>Суммаризация</span>
            Поиск информации и получение консультаций
          </button>
          <button className={styles.skillItem} onClick={e=>askQuestion("Инструкции по заключению договоров")}>
            <span className={styles.skillName}>Консультация</span>
            Инструкции по заключению договоров
          </button>
          <button className={styles.skillItem} onClick={e=>askQuestion("Формирование документов по шаблонам")}>
            <span className={styles.skillName}>Генерация документов</span>
            Формирование документов по шаблонам
          </button>
          <button className={styles.skillItem} onClick={e=>askQuestion("Анализ и визуализация табличных данных")}>
            <span className={styles.skillName}>Анализ таблиц</span>
            Анализ и визуализация табличных данных
          </button>
        </div>}
        {isFile && <FileUpload name={isFile.name} size={formatBytes(isFile.size)} cancelFile={cancelFile}/>}
          <button className={styles.skillsButton} onClick={e => setIsSkill(!isSkill)}>Навыки</button>
          <label className={styles.labelFile}>
            <IconAttach className={styles.IconAttach}/>
            <input type="file" id="file" className={styles.inputFile}
              onChange={handleChange}
              accept=".txt, .pdf, .doc, .docx, .xls, .xlsx, .csv"
            />
          </label>          
          <div className={classNames(styles.inputField, {[styles.onInput]: inputValue?.length})} 
          id="input"
          contentEditable={true} 
          data-placeholder="Спроси у Миона..." 
          ref={ input }
          onInput = {e => setInputValue(e.target.textContent)}
          onKeyDown={e => keyboardEvents(e, isFile, isDisabled)}
          onPaste={e => pasteHandler(e)}
          >     
          </div>        
        <div className={styles.sendButton}>
          <button onClick={() => ckickButton()} disabled={isDisabled} className={styles.chatButton}>
            <IconSendMessage/>
          </button>
        </div>
      </div>
    </div>
  );
};
