import styles from "./styles.module.css";
import classNames from "classnames";

export const MenuLink = ({children, isActive, onClick, label, onlyIcon}) => {
  return <button className={classNames(styles.menuLink, {[styles.isActive]: isActive})}
       onClick={onClick}>
    <div className={styles.icon}>{children}</div>
    {!onlyIcon && <div className={styles.label}>{label}</div>}
  </button>
}