import { useSelector } from 'react-redux';
import { store } from '../../store.js'
import { setError,  setErrorMessage} from '../../features/errorMessageSlice';
import styles from './styles.module.css'

export const ErrorMesage = () => {
  const errorMessage = useSelector((state) => state.errorMessage.errorMessage)
  function clickError(){
    store.dispatch(setError(false))
    store.dispatch(setErrorMessage(""))
  }
  return <div className={styles.errorWrapper}>
    <div className={styles.errorBlock}>{errorMessage}
      <button className={styles.errorButton} onClick={e=>clickError(e)}>Понятно</button>
      </div>    
    </div>
}