import Chart from 'react-apexcharts'
import styles from './styles.module.css'
import { useContext } from 'react'
import { ThemeContext } from '../../../context/ThemeContext'

export const Pie = ({table}) => {
  const {theme} = useContext(ThemeContext)
  const labelColor = () =>{
    if(theme === "Default"){
      return "#002033"
    }
    return "#FFFFFF"
  }
  let labelColorArray = table.map(el =>  labelColor())
  
  let keyArray = Object.keys(table[0])
  if(keyArray.length !== 3){
    return <div className={styles.error}>Недостаточно данных для построения круговой диаграммы</div>
  }
  const XTitle = keyArray[1]
  const YTitle = keyArray[2]

  const data = table.map(row => {
    return row[YTitle]
  })
  
  const labels = table.map(row => {
    return row[XTitle]
  }) 

  const colors =  theme === "Dark" ? ["#0070ba",	"#2fb4e9",	"#e65907",	"#6bc8c4",	"#93a445",	"#48a044",	"#cd222c", "#004596",	"#565655"] : ["#004596",	"#565655",	"#6bc8c4",	"#93a445",	"#48a044",	"#cd222c", "#0070ba",	"#2fb4e9",	"#e65907"]
  const chart = {
    series: data,
    options: {    
    chart: {
      width: 380,
      type: 'pie',
      toolbar: {
        show: true,
      }
    },
    colors: colors,    
    labels: labels,
    legend: {
      show: true,
      labels: {
        fontSize: '12px',
        colors: labelColorArray,
        fontFamily: 'Inter, serif',
        useSeriesColors: false
      },
    }
    }
  }
  return <div className={styles.chart}>
      <Chart options={chart.options} series={chart.series} type="pie"/>
    </div>
}