import { combineReducers, configureStore } from "@reduxjs/toolkit";
import openMenuLinkSlice from "./features/openMenuLinkSlice";
import sideBarIsOpenSlice from "./features/sideBarIsOpenSlice";
import errorMessageSlice from "./features/errorMessageSlice";
const rootReducer = combineReducers({
    menuLink:  openMenuLinkSlice,
    sideBarIsOpen: sideBarIsOpenSlice,
    errorMessage: errorMessageSlice,
});

export const store = configureStore({
    reducer: rootReducer,
})