import { ContentMenu } from "../../components/ContentMenu/ContentMenu"
import { Links } from "../../components/Links/Links"
import { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { useMediaQuery } from "../../hooks/useMediaQuery/index.js"
import { store } from '../../store.js'
import { setMenuLinkSlice } from '../../features/openMenuLinkSlice';
import { setSideBarIsOpenSlice } from '../../features/sideBarIsOpenSlice';
import { Logo } from '../../components/Logo/Logo';
import { IconArrowPrevious } from '@consta/icons/IconArrowPrevious';
import { useNavigate } from "react-router-dom";
import styles from "./styles.module.css";
import classnames from "classnames";

export const MainPageSidebar = () => {
  const navigate =  useNavigate()
  const sideBarIsOpen = useSelector((state) => state.sideBarIsOpen.sideBarIsOpen)
  const openSideBar = ()=>{
    store.dispatch(setSideBarIsOpenSlice(true))
  }
  const closeSideBar = ()=>{
    store.dispatch(setSideBarIsOpenSlice(false))
  }

  const toggleSideBar = ()=>{
    sideBarIsOpen ? closeSideBar() : openSideBar()
  }

  const [isSmallScreen] = useMediaQuery("screen and (max-width: 1000px)");
  const [activeMenu, setActiveMenu] = useState('chat')
  useEffect(()=> {
    if(isSmallScreen){
      closeSideBar()
    }else{
      openSideBar()
    }
  }, [isSmallScreen])

  function clickCabinet() {
    navigate('/cabinet')
  }

  function clickChat() {
    setActiveMenu('chat')
    store.dispatch(setMenuLinkSlice('chat'))
  }

  function clickMusic() {
    navigate('/music')
  }

  function clickLanding(){
    navigate('/')
  }

  return <div className={classnames({ [styles.sideBarClosed]: !sideBarIsOpen}, styles.sideBar)}>
      <button onClick={clickChat} className={styles.chatLink}>
        <Logo sideBarOpen={sideBarIsOpen}/>
      </button>
      <div className={styles.sideBarContainer}>
        <div className={styles.contentMenu}>
          <ContentMenu 
              activeMenu={activeMenu}
              sideBarOpen={sideBarIsOpen}
              openSideBar={openSideBar}
              closeSideBar={closeSideBar}                
              clickMusic={clickMusic}          
              clickChat={clickChat} 
        />
        </div>
        {!isSmallScreen && <button className={styles.toggleSideBar} 
        onClick={toggleSideBar}>
          <IconArrowPrevious/>
        </button>}
        <Links clickCabinet={clickCabinet} clickLanding={clickLanding} sideBarOpen={sideBarIsOpen}/>
      </div>    
  </div>
}