import { Table } from '@consta/uikit/Table';
import styles from './styles.module.css';
import classNames from 'classnames';

export const DataTable = ({ data }) => {
  const keys = Object.keys(data[0]);

  function rows() {
    let rows = [];
    for (let i = 0; i < data.length; i++) {
      rows.push(data[i]);
    }
    return rows;
  }

  function columns() {
    let columns = [];
    for (let i = 0; i < keys.length; i++) {
      if (keys[i] === 'id') {
        columns.push({
          title: keys[i],
          accessor: keys[i],
          sortable: true,
          hidden: true
        });
      } else {
        columns.push({
          title: keys[i],
          accessor: keys[i],
          sortable: true,
          renderCell: (row) => (
            <span className={styles.cell}>{Object.values(row)[i]}</span>
          )
        });
      }
    }
    return columns;
  }

  return (
    <Table
      rows={rows()}
      columns={columns()}
      className={classNames(styles.mionTable, styles.TableCell)}
    />
  );
};
