import Chart from 'react-apexcharts'
import styles from './styles.module.css'
import { useContext } from 'react'
import { ThemeContext } from '../../../context/ThemeContext'

export const Line = ({table}) => {  
  const {theme} = useContext(ThemeContext)
  const gridColor = () =>{
    if(theme === "Default"){
      return "#999AA9"
    }
    return "#AEB3B9"
  }
  const labelColor = () =>{
    if(theme === "Default"){
      return "#002033"
    }
    return "#FFFFFF"
  }

  let keyArray = Object.keys(table[0])
  if(keyArray.length !== 3){
    return <div className={styles.error}>Недостаточно данных для построения линейного графика</div>
  }
  const XTitle = keyArray[1]
  const YTitle = keyArray[2]

  const data = table.map(row => {
    return row[YTitle]
  })
  
  const categories = table.map(row => {
    return row[XTitle]
  })

 

  const colors =  theme === "Dark" ? ["#0070ba",	"#2fb4e9",	"#e65907",	"#6bc8c4",	"#93a445",	"#48a044",	"#cd222c", "#004596",	"#565655"] : ["#004596",	"#565655",	"#6bc8c4",	"#93a445",	"#48a044",	"#cd222c", "#0070ba",	"#2fb4e9",	"#e65907"]
  const chart = {
    options: {
    chart: {
        type: 'bar',
        height: 350
      },
      colors: colors,
      tooltip: {
        theme: theme === "Dark" ? "dark" : "light",
        y: {
          formatter: undefined,
          title: {
            formatter: (seriesName) => '',
          },
        },
      },
      dataLabels: {
        enabled: true,
        offsetY: -10,
        style: {
          fontSize: '12px',
          colors: [labelColor()],
          fontFamily: 'Inter, serif'
        },
        background: {
          enabled: false,
        }
      },
      legend: {
          show: false
        },
      xaxis: {
        categories: categories,
        labels: {
          style: {
            colors: colors,
            fontSize: '12px',
            fontFamily: 'Inter, serif'
          }
        },
        title: {
          text: XTitle,
          style: {
            color: labelColor(),
            fontSize: '12px',
            fontWeight: 400,
            fontFamily: 'Inter, serif'
          }
        }
      },
      yaxis: {
        show: true,
        labels: {
          show: true,
          style: {
            fontSize: '12px',
            colors: ["#66677D"]
          },
          formatter: (value) => { return Math.round(value) },
        },
        title: {
          text: YTitle,
          style: {
            color: labelColor(),
            fontSize: '12px',
            fontWeight: 400,
            fontFamily: 'Inter, serif'
          }
        }
      },
      grid: {
        show: true,
        borderColor: gridColor(),
      }
    },
    series: [{
      data: data
    }],

  }

  return <div className={styles.chart}>
      <Chart width="700" height="300" options={chart.options} series={chart.series} type="line"/>
    </div>
}