import styles from './styles.module.css'
import {IconTrash} from "@consta/icons/IconTrash"
import {ReactComponent as IconDoc} from "../../assets/images/doc.svg"

export const FileUpload = ({name, size, cancelFile}) => {
  return <div className={styles.fileUpload}>
    <IconDoc className={styles.iconDoc}/> 
    <div className={styles.fileName}>{name} 
      <button className={styles.deleteButton} onClick={cancelFile}>
        <IconTrash className={styles.iconTrash}/> 
      </button>
      </div>
    <div className={styles.fileSize}>{size}</div>
  </div>  
}